import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'

import './Panel.scss'

const Panel = ({ children, header, withDivider, headless, small, className }) => {
  const containerClass = cx({
    'panel__container': true,
    'panel__container--small': small
  })

  const contentClass = cx({
    'panel__content': true,
    'panel__content--headless': headless,
    'panel__content--divider': withDivider,
    [className]: !!className
  })

  return (
    <div className={containerClass}>
      {header && <h3 className='panel__header'>{header}</h3>}
      <div className={contentClass}>
        {children}
      </div>
    </div>
  )
}

export const SmallPanel = ({ children, header }) => (
  <div className='panel__container panel__container--small'>
    <div className='panel__content panel__content--small'>
      {header && <h3 className='panel__header panel__header--small'>{header}</h3>}
      {children}
    </div>
  </div>
)

Panel.defaultProps = {
  withDivider: true
}

Panel.propTypes = {
  children: PropTypes.node,
  header: PropTypes.string,
  withDivider: PropTypes.bool
}

export default Panel



