import React from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import { cleanseString } from 'Shared/helpers/string'
import './PerksGrid.scss'

export const PERKS_GRID_LAYOUTS = {
  TWO_COLUMNS: 'two-columns',
  THREE_COLUMNS: 'three-columns'
}

const PerkIcons = {
  flexible_work_hours: 'ion-md-time',
  casual_dress_code: 'ion-md-shirt',
  team_activities: 'ion-md-checkmark-circle-outline',
  music_friendly_workspace: 'ion-md-musical-notes',
  startup_culture: 'ion-md-checkmark-circle-outline',
  pet_friendly: 'ion-md-paw',
  gym: 'ion-md-checkmark-circle-outline',
  dental_coverage: 'ion-md-medical',
  free_parking: 'ion-md-car',
  foodie_heaven: 'ion-md-pizza',
  snacks: 'ion-md-checkmark-circle-outline',
  creative_space: 'ion-md-checkmark-circle-outline'
}

const PerksGrid = props => {
  const itemClass = cx({
    'column col-sm-12 col-md-6': true,
    'col-6 col-lg-6': props.layout === PERKS_GRID_LAYOUTS.TWO_COLUMNS,
    'col-4 col-lg-4': props.layout === PERKS_GRID_LAYOUTS.THREE_COLUMNS
  })
  return (
    <div className='perks-grid columns'>
      {props.data.map(perk => {
        return (
          <div key={perk.value} className={itemClass}>
            <div className='perks-grid__item'>
              <i className={`perks-grid__item__icon ${PerkIcons[perk.value]}`} />
              <div className='perks-grid__item__title'>{perk.title}</div>
              <div className='perks-grid__item__description'>{cleanseString(perk.description)}</div>
            </div>
          </div>
        )
      })}
    </div>
  )
}

PerksGrid.propTypes = {
  layout: PropTypes.oneOf([PERKS_GRID_LAYOUTS.TWO_COLUMNS, PERKS_GRID_LAYOUTS.THREE_COLUMNS]),
  data: PropTypes.arrayOf(PropTypes.shape({
    value: PropTypes.string,
    title: PropTypes.string,
    description: PropTypes.string
  }))
}

PerksGrid.defaultProps = {
  layout: PERKS_GRID_LAYOUTS.THREE_COLUMNS
}

export default PerksGrid



