import React, { Component } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import { Helmet } from 'react-helmet'
import { t } from 'i18next'
import { unless, isEmpty, isNil, prop } from 'ramda'
import { JSONLD, Generic } from 'react-structured-data'
import scriptLoader from 'react-async-script-loader'

import { DEFAULT_JOB_OFFER_LOCATION_ZOOM } from 'Shared/constants/Job'
import { NOT_AVAILABLE } from 'Shared/constants/Placeholders'

import { parseLocationCoords } from 'Shared/helpers/Location'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { baseUrl, validateProtocol } from 'Shared/helpers/string'
import { hasAnySocialMedia, mapSocialMedia } from 'Shared/helpers/Organisation'
import { showPropOr } from 'Shared/helpers/responseData'

import Container from 'Shared/components/Container'
import Panel from 'Shared/components/Panel/Panel'
import Text from 'Shared/components/Text/Text'
import Label from 'Shared/components/FormElements/Label/Label'
import LocationMap from 'Shared/components/LocationMap'
import Divider from 'Shared/components/FormElements/Divider'
import PerksGrid, { PERKS_GRID_LAYOUTS } from 'Employer/components/PerksGrid/PerksGrid'
import CompanyVideo from 'Shared/components/CompanyDetailsSidebar/components/CompanyVideo/CompanyVideo'
import CompanyGallery from 'Shared/components/CompanyDetailsSidebar/components/CompanyGallery/CompanyGallery'

import CompanyProfileHeaderConnector from 'Employer/connectors/Components/CompanyProfileHeaderConnector'
import ActiveJobOfferTilesConnector from 'Employer/connectors/Components/ActiveJobOfferTilesConnector'
import { CompanyProfileViewLoader } from 'Shared/components/Main/MainLoaders'

import ogImage from 'Shared/assets/images/brand/open-graph-wide.jpg'
import './CompanyProfileView.scss'

class CompanyProfileView extends Component {
  static propTypes = {
    canUseCompanyPageService: PropTypes.bool.isRequired,
    companySize: PropTypes.object.isRequired,
    fetchPerks: PropTypes.func.isRequired,
    fetchIndustries: PropTypes.func.isRequired,
    fetchOrganisation: PropTypes.func.isRequired,
    fetchCompanySize: PropTypes.func.isRequired,
    organisation: PropTypes.shape({
      address: PropTypes.string,
      locationCoords: PropTypes.string,
      perks: PropTypes.arrayOf(PropTypes.string)
    })
  }

  state = {
    ready: false
  }

  async componentDidMount () {
    await this.props.fetchOrganisation()
    await this.props.fetchPerks()
    await this.props.fetchIndustries()
    await this.props.fetchCompanySize()
  }

  componentWillReceiveProps ({ isScriptLoaded, isScriptLoadSucceed }) {
    if (isScriptLoaded && isScriptLoadSucceed) {
      this.setState({ ready: true })
    }
  }

  get perksLayout () {
    const { TWO_COLUMNS, THREE_COLUMNS } = PERKS_GRID_LAYOUTS

    return this.isLayoutFullWidth() ? THREE_COLUMNS : TWO_COLUMNS
  }

  isLayoutFullWidth = () => {
    const { organisation, canUseCompanyPageService, companyImages } = this.props

    const socialMediaLinks = unless(isNil, prop('socialMediaLinks'))(organisation) || {}

    return (
      (isNil(organisation.socialMediaLinks) || !hasAnySocialMedia(socialMediaLinks)) &&
      (isNil(organisation.website) || isEmpty(organisation.website)) &&
      (!canUseCompanyPageService || (!organisation.videoUrl && isEmpty(companyImages)))
    )
  }

  hasSecondarySidebarSection = () => {
    const { organisation } = this.props
    const socialMediaLinks = unless(isNil, prop('socialMediaLinks'))(organisation) || {}

    return !!organisation.website || hasAnySocialMedia(socialMediaLinks)
  }

  getWebsiteDetails = website => {
    const url = !validateProtocol(website)
      ? website
      : 'https://' + website
    return (
      <div>
        <Label>{t('common.website')}</Label>
        <a className='link--dark' href={url} target={'_blank'}>{baseUrl(website)}</a>
      </div>
    )
  }

  getCompanyVideo = videoUrl => <CompanyVideo videoUrl={videoUrl} />
  getCompanyGallery = images => <CompanyGallery images={images} />

  getCompanySocialMedia = socialMediaLinks => (
    <div>
      <h4 className='company-details-sidebar__heading'>{t('common.socialProfiles')}</h4>
      {mapSocialMedia(socialMediaLinks).map(medium => {
        return <a href={medium.url} target={'_blank'} className='company-details-sidebar__social-medium'>
          <i className={`icon-im-contact-${medium.type}`} /> {t(`socialMedia.${medium.type}`)}
        </a>
      })}
    </div>
  )

  get pageTitle () {
    const { organisation } = this.props
    return t('seo.companyProfile.title', { name: organisation.name })
  }

  render () {
    const {
      organisation,
      companySize,
      perks,
      canUseCompanyPageService,
      companyImages
    } = this.props

    if (isNil(organisation)) {
      return <CompanyProfileViewLoader />
    }

    const mainClass = cx({
      'column': true,
      'col-12': this.isLayoutFullWidth(),
      'col-8 col-md-12': !this.isLayoutFullWidth() || this.hasSecondarySidebarSection()
    })

    const socialMediaLinks = unless(isNil, prop('socialMediaLinks'))(organisation) || {}

    return (
      <section className='company-profile__container parsing--hidden'>
        <Helmet>
          <title>{this.pageTitle}</title>
          <meta property='og:title' content={this.pageTitle} />
          <meta property='og:image' content={ogImage} />
          <meta property='og:url' content={window.location.href} />
        </Helmet>
        <CompanyProfileHeaderConnector
          canUseCompanyPageService={canUseCompanyPageService}
          organisation={organisation}
          scrapingSource={organisation.scrapingSource}
          companySize={companySize}
        />
        <Container className='company-details-view__wrapper'>
          <section className='columns'>
            <main className={mainClass}>
              <Panel headless withDivider={false}>
                <h3>{t('company.description')}</h3>
                <Text>
                  {isNil(organisation.description)
                    ? t('company.descriptionPlaceholder')
                    : parseHtmlWithoutSpan(organisation.description)}
                </Text>
                {!isEmpty(perks) && (
                  <div>
                    <Divider />
                    <h3>{t('company.perks')}</h3>
                    <PerksGrid data={perks} layout={this.perksLayout} />
                  </div>
                )}
                {!isNil(organisation.city) && this.state.ready && (
                  <div>
                    <Divider />
                    <h3>{t('company.location')}</h3>
                    <div className='company-profile__location'>
                      <div className='job-offer-details__location--address'>{organisation.address}</div>
                      {/* <LocationMap
                        defaultZoom={DEFAULT_JOB_OFFER_LOCATION_ZOOM}
                        location={parseLocationCoords(organisation.locationCoords)}
                        loadingElement={<div style={{ height: `100%` }} />}
                        containerElement={<div style={{ height: `200px`, marginTop: '22px', marginBottom: '48px' }} />}
                        mapElement={<div style={{ height: `100%` }} />}
                      /> */}
                    </div>
                  </div>
                )}
              </Panel>
              <ActiveJobOfferTilesConnector
                isLayoutFullWidth={this.isLayoutFullWidth()}
                organisation={organisation}
              />
            </main>
            {!this.isLayoutFullWidth() && (
              <aside className='column col-4 col-md-12'>
                {canUseCompanyPageService && (organisation.videoUrl || !isEmpty(companyImages)) &&
                <Panel small headless className='panel__content--sidebar' withDivider={false}>
                  { organisation.videoUrl && this.getCompanyVideo(organisation.videoUrl) }
                  {organisation.videoUrl && !isEmpty(companyImages) && <Divider className='form__divider--sidebar' />}
                  {!isEmpty(companyImages) && this.getCompanyGallery(companyImages)}
                </Panel>}
                {this.hasSecondarySidebarSection() && (
                  <Panel small headless className='panel__content--sidebar' withDivider={false}>
                    {organisation.website && this.getWebsiteDetails(organisation.website)}
                    {organisation.website && hasAnySocialMedia(socialMediaLinks) && <Divider className='form__divider--sidebar' />}
                    {hasAnySocialMedia(socialMediaLinks) && this.getCompanySocialMedia(socialMediaLinks)}
                  </Panel>
                )}
              </aside>
            )}
          </section>
          {!isNil(organisation) && (
            <JSONLD>
              <Generic type='hiringOrganization' jsonldtype='Organization' schema={
                {
                  name: organisation.name,
                  sameAs: showPropOr('website', NOT_AVAILABLE, organisation),
                  logo: organisation.logoUrl,
                  url: organisation.website || window.location.href
                }
              }>
                <Generic type='location' jsonldtype='Place'>
                  <Generic type='address' jsonldtype='PostalAddress' schema={{
                    addressLocality: showPropOr('city', NOT_AVAILABLE, organisation),
                    addressCountry: showPropOr('country', NOT_AVAILABLE, organisation),
                    streetAddress: showPropOr('address', NOT_AVAILABLE, organisation)
                  }} />
                </Generic>
              </Generic>
            </JSONLD>
          )}
        </Container>
      </section>
    )
  }
}

export default scriptLoader(`https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_PLACES_JAVASCRIPT_API_KEY}&libraries=places&language=en`)(CompanyProfileView)



