import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { ifElse, isNil, propOr, where } from 'ramda'

import { getCountryNameByISOCode } from 'Shared/helpers/Country'
import Container from 'Shared/components/Container'

import fallbackBackground from 'Shared/assets/images/brand/oliv-pattern.png'
import DEFAULT_PHOTO_URL from 'Shared/assets/images/other/avatar-default-employer.png'
import './CompanyProfileHeader.scss'

const CompanyProfileHeader = ({ organisation, canUseCompanyPageService, ...props }) => {
  const cityCountryLine = () => {
    const hasBasicLocation = where({
      city: city => !isNil(city),
      country: country => !isNil(country)
    })

    return ifElse(
      hasBasicLocation,
      () => `${organisation.city}, ${getCountryNameByISOCode(organisation.country)}`,
      () => props.t('company.locationNotSpecified')
    )(organisation)
  }

  const bgImageStyles = () => {
    switch (true) {
      case canUseCompanyPageService:
        return {
          backgroundImage: `url(${organisation.coverPhotoUrl || fallbackBackground})`
        }
      default:
        return {
          backgroundImage: `url(${fallbackBackground})`
        }
    }
  }

  const logoStyles = {
    backgroundImage: `url(${organisation.logoUrl || DEFAULT_PHOTO_URL})`
  }

  return (
    <div>
      <div className='company-profile-header__expander' style={bgImageStyles()} />
      <div className='company-profile-header'>
        <Container className='company-profile-header__container'>
          <div className='company-profile-header__logo' alt={`${organisation.name} - Logo`} title={`${organisation.name} - Logo`} style={logoStyles} />
          <div className='company-profile-header__details'>
            {
              canUseCompanyPageService ? <h1 className='company-profile-header__details-heading'>{organisation.name}</h1> : <div className='company-profile-header__details-heading'>{organisation.name}</div>
            }

            {!props.scrapingSource && <h5 className='company-profile-header__details-industry'>{props.industry}</h5>}
            {!props.scrapingSource && <div className='company-profile-header__details-text'>{cityCountryLine()}</div>}
            <div className='company-profile-header__details-text'>{propOr('', ['companySize', organisation.size, 'name'], props)}</div>
          </div>
        </Container>
      </div>
    </div>
  )
}

export default withTranslations(CompanyProfileHeader)



