import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CompanyProfileHeader from 'Employer/components/CompanyProfileHeader/CompanyProfileHeader'
import { getOrganisationIndustryNameById } from 'Shared/selectors/organisations'

const mapStateToProps = (state, ownProps) => ({
  industry: getOrganisationIndustryNameById(state, ownProps.match.params.id)
})

const mapDispatchToProps = dispatch => ({})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompanyProfileHeader))



