import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ActiveJobOfferTiles from 'Employer/components/ActiveJobOfferTiles/ActiveJobOfferTiles'

import { COMPANY_DETAILS_JOB_OFFERS_INCLUDE, COMPANY_DETAILS_JOB_OFFERS_COUNT_INCLUDE } from 'Shared/constants/Organisation'
import { Statuses } from 'Shared/constants/Job'

import { fetchOrganisationJobOffersByOrganisationId, fetchOrganisationJobOffersCountByOrganisationId } from 'Shared/ducks/jobOffers'
import { getEmployersJobOffersByStatus, getJobOffersCountByOrganisationId } from 'Shared/selectors/jobOffers'

const mapStateToProps = (state, ownProps) => ({
  jobsCount: getJobOffersCountByOrganisationId(ownProps.match.params.id)(state),
  jobOffers: getEmployersJobOffersByStatus(state, ownProps.match.params.id, Statuses.active)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchJobsCount: () => dispatch(fetchOrganisationJobOffersCountByOrganisationId(
    ownProps.match.params.id,
    COMPANY_DETAILS_JOB_OFFERS_COUNT_INCLUDE
  )),
  fetchActiveJobOffers: initialInclude => dispatch(fetchOrganisationJobOffersByOrganisationId(
    ownProps.match.params.id,
    COMPANY_DETAILS_JOB_OFFERS_INCLUDE(initialInclude)))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ActiveJobOfferTiles))



