import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { fetchCompanySize, fetchPerks } from 'Shared/ducks/resources'
import CompanyProfileView from 'Candidate/views/CompanyProfileView'
import { fetchIndustries } from 'Shared/ducks/industries'
import { fetchOrganisation } from 'Shared/ducks/organisations'
import { getOrganisationPerksById, getOrganisation } from 'Shared/selectors/organisations'
import { hasOrganisationCompanyPageAvailableByIdPublic } from 'Shared/selectors/membership'
import { getCompanySizesEntitiesByValue } from 'Shared/selectors/resources'
import { getAllOrganisationImages } from 'Shared/selectors/files'

const mapStateToProps = (state, ownProps) => ({
  userRole: state.user.role,
  organisation: getOrganisation(state, ownProps.match.params.id),
  canUseCompanyPageService: hasOrganisationCompanyPageAvailableByIdPublic(ownProps.match.params.id)(state),
  companySize: getCompanySizesEntitiesByValue(state),
  perks: getOrganisationPerksById(ownProps.match.params.id)(state),
  companyImages: getAllOrganisationImages(state, ownProps.match.params.id)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchPerks: () => dispatch(fetchPerks()),
  fetchIndustries: () => dispatch(fetchIndustries()),
  fetchOrganisation: () => dispatch(fetchOrganisation(ownProps.match.params.id)),
  fetchCompanySize: () => dispatch(fetchCompanySize())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompanyProfileView))



