import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { isEmpty } from 'ramda'
import cx from 'classnames'
import { t } from 'i18next'
import { sortByDateAscWithField } from 'Shared/helpers/date'
import JobOfferCard from 'Shared/components/JobOfferCard'
import { INITIAL_ACTIVE_JOB_OFFERS } from 'Shared/constants/Organisation'
import Button from 'Shared/components/Button'

class ActiveJobOfferTiles extends PureComponent {
  state = {
    limited: true
  }
  static propTypes = {
    isLayoutFullWidth: PropTypes.bool,
    organisation: PropTypes.object.isRequired
  }
  async componentDidMount () {
    await this.props.fetchJobsCount()
    await this.props.fetchActiveJobOffers({limit: INITIAL_ACTIVE_JOB_OFFERS})
  }

  loadMoreJobs = async () => {
    await this.props.fetchActiveJobOffers()
    return this.setState({ limited: false })
  }

  get jobOffersToRender () {
    const sortedJobOffers = this.props.jobOffers.sort(sortByDateAscWithField('publishedAt')).reverse()

    return this.state.limited
      ? sortedJobOffers.slice(0, INITIAL_ACTIVE_JOB_OFFERS)
      : sortedJobOffers
  }

  shouldDisplayMoreBtn = jobsCount => this.state.limited && jobsCount > INITIAL_ACTIVE_JOB_OFFERS

  renderJobOffers = () => {
    const { organisation, isLayoutFullWidth, jobsCount } = this.props

    const cardClass = cx({
      column: true,
      'column col-4 col-lg-6 col-sm-12': isLayoutFullWidth,
      'column col-6 col-sm-12': !isLayoutFullWidth
    })

    return (
      <div>
        <h3 className='company-details-view__heading'>{t('common.activeJobs', { count: jobsCount })}</h3>
        <div className='columns'>
          {this.jobOffersToRender.map((jobOffer) => (
            <div className={cardClass} key={`active-offer-${jobOffer.id}`}>
              <JobOfferCard key={jobOffer.id} jobOffer={jobOffer} organisation={organisation} />
            </div>
          ))}
        </div>
        <div className='text-center'>
          {this.shouldDisplayMoreBtn(jobsCount) && <Button onClick={this.loadMoreJobs}>{t('common.showMore')}</Button>}
        </div>
      </div>
    )
  }

  render () {
    return isEmpty(this.jobOffersToRender)
      ? null
      : this.renderJobOffers()
  }
}
export default ActiveJobOfferTiles



